
.bk-popconfirm-content {
    font-size: 12px;

    .popconfirm-split {
        display: inline-block;
        position: absolute;
        top: 9px;
        background-color: #dcdee5;
        width: 1px;
        height: 12px;
        margin-left: -2px;
    }
    &.popconfirm-more {
        padding: 2px 5px;
        .popconfirm-content {
            margin: 2px 0 8px;
        }
        .popconfirm-operate {
            text-align: right;
            margin-bottom: 2px;
        }
    }
    .popconfirm-operate {
        button {
            background: transparent;
            border: transparent;
            color: #3a84ff;

            &:hover {
                color: #699df4;
            }
            &[type="button"] {
                background: #3a84ff;
                border: #3a84ff;
                border-radius: 2px;
                color: #fff;
                &:hover {
                    background-color: #699df4;
                    border-color: #699df4;
                }
            }
        }
        .default-operate-button {
            padding: 1px 6px;
        }
    }
}
